@import './public/index.scss';

.search {

  height: 220px;

  background-color: white;

  display: flex;
  flex-direction: column;
  align-items: center;

  border-bottom: 1px solid #444444;

  @media (max-width: $screen-xs-max) {
    height: 260px;
  }

  &__title {
    font-size: 30px;
    font-weight: 700;

    padding-top: 30px;
  }
  &__input {

    padding-top: 30px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}