.link {
  text-decoration: none;
  color: inherit;

  &:visited {
    color: inherit;
  }

  &:active {
    color: inherit;
  }
}

.bookItem {
  width: 100%;
  height: 100%;
  max-height: 460px;
  background-color: white;

  border-radius: 10px;

  line-height: 1.5em;

  box-shadow: 5px 5px 20px #aaaaaa;


  &__wrapper {
    box-sizing: border-box;

    height: 100%;
    padding: 12px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__info {
    flex: 0 0 85%;

    display: flex;
    flex-direction: column;
  }

  &__image {
    flex: 0 0 50%;

    display: flex;
    justify-content: center;
    align-items: center;


    img {
      width: 128px;
      box-shadow: 10px 10px 20px #aaaaaa;
    }
  }

  &__authors {
    flex: 0 0 8%;

    text-align: center;
    font-weight: 400;
    color: #444;

    padding-top: 10px;

    max-height: 60px;
  }

  &__title {

    text-align: center;
    font-weight: 900;
    padding-top: 20px;

    font-size: 20px;

    height: 100px;

  }

  &__categories {

    font-size: 14px;
    line-height: 1.5em;

    color: #888;
  }
}